.section-spacing {
  margin-bottom: 20px;
}

.section-divider {
  border-bottom: 1px solid #e5e7eb;
}

/* Thematic section backgrounds */
.bg-section-red {
  /* subtle faded red */
  background: linear-gradient(
    180deg,
    rgba(179, 25, 66, 0.06) 0%,
    rgba(179, 25, 66, 0.03) 100%
  );
}

.bg-section-blue {
  /* subtle faded blue */
  background: linear-gradient(
    180deg,
    rgba(10, 49, 97, 0.06) 0%,
    rgba(10, 49, 97, 0.03) 100%
  );
}

/* Stars pattern over soft white base */
.bg-section-stars {
  background-color: rgba(255, 255, 255, 0.6);
  background-image:
    radial-gradient(circle at 10px 10px, rgba(179, 25, 66, 0.25) 1px, transparent 1.5px),
    radial-gradient(circle at 30px 25px, rgba(10, 49, 97, 0.25) 1px, transparent 1.5px);
  background-size: 40px 40px, 60px 60px;
}

@media (prefers-color-scheme: dark) {
  .bg-section-red {
    background: linear-gradient(
      180deg,
      rgba(179, 25, 66, 0.12) 0%,
      rgba(179, 25, 66, 0.06) 100%
    );
  }
  .bg-section-blue {
    background: linear-gradient(
      180deg,
      rgba(10, 49, 97, 0.12) 0%,
      rgba(10, 49, 97, 0.06) 100%
    );
  }
  .bg-section-stars {
    background-color: rgba(20, 20, 20, 0.2);
    background-image:
      radial-gradient(circle at 10px 10px, rgba(179, 25, 66, 0.35) 1px, transparent 1.5px),
      radial-gradient(circle at 30px 25px, rgba(10, 49, 97, 0.35) 1px, transparent 1.5px);
  }
}

/* About section title: enforce single-line, fluid responsive sizing */
.about-title {
  /* Keep on one line */
  white-space: nowrap;
  /* Tight but readable line height */
  line-height: 1.15;
  /* Fluid type: scales with viewport; !important to override utility sizes */
  font-size: clamp(1rem, 2.2vw + 0.25rem, 2.75rem) !important;
  /* Slightly tighter tracking similar to tracking-tight */
  letter-spacing: -0.01em;
}

/* Team member images: ensure top-cropped alignment consistently */
.team-image {
  object-fit: cover;
  object-position: top !important;
}